"use client"
import { supabase } from "@lib/supabase";
import { useEffect, useState } from "react"
// Map skill int to label
const skillMap: Record<number, string> = {
  1: 'Beginner',
  2: 'Intermediate',
  3: 'Advanced',
};

const statusColors: Record<string, string> = {
  'In Progress': 'text-blue-400',
  'Complete': 'text-green-400',
  'Pending': 'text-yellow-400',
  'Approved': 'text-green-500',
  'Rejected': 'text-red-400',
};

export default function DashboardPage() {
  const [videos, setVideos] = useState<any[]>([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    async function fetchVideos() {
      // Fetch videos and join with profiles if needed
      const { data, error } = await supabase
        .from('videos')
        .select('id, video_title, skill, created_at');
      if (!error) setVideos(data || []);
      setLoading(false);
    }
    fetchVideos();
  }, []);

  if (loading) return <div className="p-8 text-white">Loading...</div>;

  return (
    <div className="flex min-h-screen bg-[#18181b] text-white">
      {/* Sidebar (simplified for brevity) */}
      <aside className="w-64 bg-[#232329] p-6 flex flex-col">
        <div className="mb-8 font-bold text-lg">Michael Dutro</div>
        <nav className="flex-1">
          <div className="mb-4">
            <div className="text-gray-400 uppercase text-xs mb-2">Dashboards</div>
            <ul>
              <li className="mb-2">
                <a className="flex items-center text-white bg-[#27272f] rounded px-3 py-2" href="#">Home</a>
              </li>
              <li>
                <a className="flex items-center text-gray-400 hover:text-white px-3 py-2" href="#">Files</a>
              </li>
            </ul>
          </div>
          <div>
            <div className="text-gray-400 uppercase text-xs mb-2">Pages</div>
            <ul>
              <li><a className="flex items-center text-gray-400 hover:text-white px-3 py-2" href="#">User Profile</a></li>
              <li><a className="flex items-center text-gray-400 hover:text-white px-3 py-2" href="#">Overview</a></li>
              <li><a className="flex items-center text-gray-400 hover:text-white px-3 py-2" href="#">Projects</a></li>
              <li><a className="flex items-center text-gray-400 hover:text-white px-3 py-2" href="#">Campaigns</a></li>
              <li><a className="flex items-center text-gray-400 hover:text-white px-3 py-2" href="#">Documents</a></li>
              <li><a className="flex items-center text-gray-400 hover:text-white px-3 py-2" href="#">Followers</a></li>
              <li><a className="flex items-center text-gray-400 hover:text-white px-3 py-2" href="#">Account</a></li>
            </ul>
          </div>
        </nav>
      </aside>

      {/* Main Content */}
      <main className="flex-1 p-10">
        <div className="mb-6">
          <h1 className="text-2xl font-semibold">Video List</h1>
        </div>
        <div className="bg-[#232329] rounded-lg overflow-hidden">
          <table className="min-w-full text-sm">
            <thead>
              <tr className="bg-[#232329] text-gray-400">
                <th className="px-4 py-3 text-left">Video ID</th>
                <th className="px-4 py-3 text-left">User</th>
                <th className="px-4 py-3 text-left">Video Title</th>
                <th className="px-4 py-3 text-left">Recommendation Level</th>
                <th className="px-4 py-3 text-left">Date</th>
                <th className="px-4 py-3 text-left">Upload Status</th>
              </tr>
            </thead>
            <tbody>
              {videos.map((video, idx) => (
                <tr key={video.id} className="border-b border-[#2c2c34] hover:bg-[#23232b]">
                  <td className="px-4 py-3">{`#CM${9800 + idx + 1}`}</td>
                  <td className="px-4 py-3">Name</td> {/* Replace with user if available */}
                  <td className="px-4 py-3">{video.video_title}</td>
                  <td className="px-4 py-3">{skillMap[video.skill] || 'Beginner'}</td>
                  <td className="px-4 py-3">{new Date(video.created_at).toLocaleDateString()}</td>
                  <td className="px-4 py-3">
                    <span className={statusColors['In Progress']}>‚óè</span> In Progress
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>
        {/* Pagination (static for now) */}
        <div className="flex justify-end mt-4">
          <div className="flex space-x-2">
            <button className="bg-[#232329] text-gray-400 px-3 py-1 rounded">Button</button>
            <button className="bg-[#232329] text-gray-400 px-3 py-1 rounded">2</button>
            <button className="bg-[#232329] text-gray-400 px-3 py-1 rounded">3</button>
            <button className="bg-[#232329] text-gray-400 px-3 py-1 rounded">4</button>
            <button className="bg-[#232329] text-gray-400 px-3 py-1 rounded">5</button>
          </div>
        </div>
      </main>
    </div>
  );
}
